@import "smart-grid";
.adminBlock {
  a {
    font:18px roboto;
    text-decoration: none;
    color:#F7616B;
  }
}

.formAuth {
  h2 {
    margin-top: 25px;
    font:48px lora;
    margin-bottom: 25px;
    text-align: center;
    .sm-block({
        font-size: 36px;
    });
    form {
      max-width: 450px;
      label {
        display: block;
        width: 100%;
        font:22px roboto;

      }
      input {
      display: block;
        height: 30px;
        width: 100%;
        outline: none;
        border:1px solid #555;
        text-indent: 15px;
        font:14px roboto;
        color:#555;
      }
    }
  }

}